import { DefaultButton, DivideUnderLine } from 'components/shared/styles';
import styled from 'styled-components';
import PropType from 'prop-types';

function Order({ checkedList }) {
  const getExpectedPrice = () =>
    checkedList.reduce(
      (sum, current) => sum + Number(current.price) * current.quantity,
      0,
    );

  return (
    <Styled.OrderContainer>
      <Styled.OrderTitleWrapper>
        <Styled.OrderTitle></Styled.OrderTitle>
      </Styled.OrderTitleWrapper>
      <Styled.UnderLine />
      <div>
        <Styled.ExpectedPriceWrapper>
          <Styled.HilightText></Styled.HilightText>
          <Styled.HilightText>{`${getExpectedPrice()}`}</Styled.HilightText>
        </Styled.ExpectedPriceWrapper>
        <Styled.OrderButtonWrapper>
          <Styled.OrderButton>{`(${checkedList.length})`}</Styled.OrderButton>
        </Styled.OrderButtonWrapper>
      </div>
    </Styled.OrderContainer>
  );
}

export default Order;

Order.propTypes = {
  checkedList: PropType.arrayOf(
    PropType.shape({
      id: PropType.string.isRequired,
      price: PropType.string.isRequired,
      quantity: PropType.number.isRequired,
      src: PropType.string.isRequired,
      title: PropType.string.isRequired,
    }),
  ).isRequired,
};

const Styled = {
  OrderContainer: styled.section`
    width: 35%;
    height: 260px;
    margin-left: 5%;
    margin-top: 100px;
    border: 1px solid #dddddd;
  `,
  OrderTitleWrapper: styled.div`
    display: flex;
    align-items: center;
    padding: 16px 20px;
  `,
  OrderTitle: styled.h3`
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: 400;
  `,
  UnderLine: styled(DivideUnderLine)`
    border: 1px solid #dddddd;
  `,
  ExpectedPriceWrapper: styled.div`
    display: flex;
    justify-content: space-between;
    padding: 20px;
    margin-top: 20px;
  `,
  HilightText: styled.span`
    position: relative;
    font-weight: 700;
    display: inline-block;
    text-align: center;
    padding: 0 2px;
    font-size: 20px;

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 8px;
      background-color: #2ac1bc;
      opacity: 0.5;
      z-index: -1;
    }
  `,
  OrderButtonWrapper: styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px 10px 0 10px;
  `,
  OrderButton: styled(DefaultButton)`
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #2ac1bc;
    font-size: 24px;
    color: white;
    width: 100%;
    padding: 20px;
  `,
};
